<template>
  <div>
    <div style="margin-top:.86rem" v-if="isSkeletonLoading">
      <skeleton :nums="4" :iheight="'0.4rem'"/>
    </div>
    <div v-else>
       <!-- 分类内容区域 -->
      <div class="classify-conent">
        <!-- 左侧分类按钮 -->
        <div class="classify-left-btn">
          <div :id="`left-item${index}`" :class="{'classify-left-item':true, 'classify-item--active':activeIndex === index,'classify-left-item--active':(activeIndex - 1) === index}" v-for="(item, index) in leftNavData" :key="index" @click="categoryTab(item, index)">
            <div :class="{'classify-item-bg':activeIndex === index}">
              <div :class="{'classify-left-name':true}">{{item.navigationName}}</div>
              <div class="classify-left-img" v-if="activeIndex === index">
                <img :src="`${$cdnImageUrl}/classify/navBgNew.png`" />
              </div>
            </div>
          </div>
        </div>
        <!-- 右侧内容区域 -->
        <div :class="{'classify-right-link':true, 'classify-firstItem--active': activeIndex === 0, 'classify-lastItem--active': activeIndex === (leftNavData.length - 1)}">
           <van-swipe ref='vanSwiper' :autoplay="3000" indicator-color="white" style="width: 100%;" class="classify-banner" v-if="swiperItemdata.length > 0">
              <van-swipe-item
                :key="index"
                @click="swiperTypeLink(item)"
                v-for="(item, index) in swiperItemdata"
              >
                <img :src="item.imageUrl | imageToHttps" alt />
              </van-swipe-item>
            </van-swipe>
          <div
            class="category-ground"
            v-for="(item, index) in categorys"
            :key="index">
            <p class="classifyRight-title">{{item.navigationName}}</p>
            <div class="category-list">
              <div
                @click="goodsDetailLink(els)"
                class="category-containers"
                v-for="(els, i) in item.children"
                :key="i"
              >
                <div class="category-containers-block" v-if="!els.hidden">
                  <img :src="els.imageUrl | imageToHttps" alt class="classify-goods-img" />
                </div>
                <div class="classify-goods-name">{{ item.id === els.parentId ? els.navigationName : '' }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import skeleton from '@/components/skeleton'
import commons from '@/utils/commons.js'
import { categoryLeftnavData, categoryRightListData } from '@/http/service/category'
export default {
  props: ['currentCateId'],
  components: {
    skeleton,
  },
  data() {
    return {
      isSkeletonLoading: true,
      activeCategoryId: '',
      activeBtn: 0,
      categoryTitle: '推荐分类',
      leftNavData: [],
      rbtnData: [],
      swiperItemdata: [],
      categoryList: [],
      isImgarr: true,
      activeIndex: 0,
      categorys: []
    }
  },
  mounted() {
    categoryLeftnavData().then(res => {
      this.isSkeletonLoading = false
      if (!res.data || !res.data.data || !res.data.data.length) {
        return
      }
      let currentIndex = res.data.data.findIndex(item => item.id + '' === this.currentCateId);
      let defaultId = res.data.data[0].id
      if (currentIndex > 0) {
        defaultId = res.data.data[currentIndex].id
        this.activeCategoryId = defaultId
        this.activeIndex = currentIndex
      }
      this.leftNavData = res.data.data
      this.getCategory(defaultId)
      this.$nextTick(() => {
        document.getElementById(`left-item${this.activeIndex}`).scrollIntoView();
      });
    })
    commons.swipeResize(this, 'vanSwiper')
  },
  methods: {
    /**
     * @description 分类tab切换
     */
    categoryTab(item, index) {
      if (this.activeCategoryId === item.id) {
        return;
      }
      this.activeIndex = index
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true
      })
      this.activeCategoryId = item.id;
      this.activeBtn = index
      this.categoryTitle = item.navigationName
      this.getCategory(item.id).finally(() => {
        this.$toast.clear()
      })
    },
    /**
     * @description 分类信息跳转 搜索列表
     */
    goodsDetailLink(els) {
      console.log(els.url, '跳转')
      const paramsArray = els.url.split('&');
      const params = {};
      if (paramsArray.length > 0) {
        for (const val of paramsArray) {
          params[val.split('=')[0]] = val.split('=')[1];
        }
      }
      let isTrue = false;
      const { productCateId, brandId, attributeId } = params;
      isTrue = brandId || attributeId;
      if (productCateId || brandId || attributeId) {
        let filterData = `hasStock@${brandId || 'all'}@0${attributeId ? `@${attributeId}` : ''}`;
        filterData = isTrue ? filterData : ''
        // console.log(brandId, attributeId, brandId || attributeId, '0000000000000')
        this.$router.push({
          path: '/categorySearch',
          query: {
            productCateId: productCateId,
            filterData: filterData,
          },
        })
      }
    },
    /**
     * @description 分类信息跳转 搜索列表
     */
    swiperTypeLink(item) {
      console.log('bannerClick:', item)
      const { linkType, url } = item
      const relationId = item.bannerId ? item.bannerId : item.relationId ? item.relationId : item.id
      console.log(relationId)
      this.$store.dispatch('bannerClick', { hyperLinkType: linkType + '', hyperLink: url, relationId });
    },
    // 获取分类
    getCategory (activeCategoryId) {
      return categoryRightListData({ id: activeCategoryId }).then(res => {
        /* { levels === 1 则为分类banner   === 2 分类} */
        let resData = res.data.data || []
        this.swiperItemdata = resData.filter(item => item.levels === '1')
        let fatherItem = resData.filter(c => {
          return (c.parentId === activeCategoryId && c.levels === '2')
        })
        this.categorys = fatherItem.map(c => {
          let children = resData.filter(_c => {
            return _c.parentId === c.id
          })
          let remainder = (children.length * 3 - 1) % 3
          for (let i = 0; i < remainder; i++) {
            children.push({
              hidden: true
            })
          }
          return {
            ...c,
            children
          }
        })
      })
    }
  },
}
</script>
