<template>
  <div class="classify">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <Header v-if="isUplus" :leftArrow="isUplus"></Header>
      <!-- 分类搜索 -->
      <div class="search-classify" v-if="!isUplus">
        <div class="search-jump">
          <div class="search-address">
            <div @click="locationBtn()">
              <img class="search-imgs" src="data:image/png;base64," alt="">
              <span class="townshop-text">{{location.district}}</span>
            </div>
          <van-popup v-model="showChooseLoaction" position="bottom" @close="onHide" @click="onHide">
            <!-- <change-geographic-location
              ref='geoLocation'
              @handleLocation="handleLocation"
              @onHide="onHide"
            /> -->
          </van-popup>
          </div>
          <div @click="hotKeyLink()" class="search-input">
            <img class="search-input-img" :src="$globalIcon.commonSearchIcon" alt="">
            <span class="search-hotwords">{{hotwords}}</span>
          </div>
          <span @click="toService()">
            <div class="service-container">
              <span class="kefu-icon"></span>
              <span class="kefu-text">客服</span>
            </div>
          </span>
          <!-- <div class="searchNews">3</div> -->
        </div>
      </div>
      <classifyTab :currentCateId="activeCateId"/>
      <!-- 地址选择 -->
      <lazy-addr-list ref="addrList" @handleLocation="handleLocation"></lazy-addr-list>
    </div>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
import { defaultSearch } from '@/http/service/search'
import classifyTab from './components/classify-tab';
// const changeGeographicLocation = () => import('@/components/change-geographic-location/change-geographic-location')
const lazyAddrList = () => import('@/components/addrChooseList/addrList.vue')

const localData = {
  locationData: [],
  scrollPosition: 0,
};
export default {
  components: {
    skeleton,
    Header,
    classifyTab,
    lazyAddrList
  },
  data() {
    return {
      skeletonLoading: true,
      // searchComponentLoad: true,
      // searchComponentShow: false,
      isUplus: this.$util.isHaierApp(),
      hotwords: '',
      showChooseLoaction: false,
      township: '',
      addrLoad: false,
      activeCateId: '',
    }
  },
  beforeRouteLeave (to, from, next) {
    if (document.querySelector('.classify-left-btn')) {
      localData.scrollPosition = document.querySelector('.classify-left-btn').scrollTop
    }

    next();
  },
  activated() {
    const top = localData.scrollPosition
    // 跳转到指定位置
    this.$nextTick(() => {
      // TODO: 解决必须使用setTimeOut的问题
      setTimeout(() => {
        document.querySelector('.classify-left-btn') && document.querySelector('.classify-left-btn').scrollTo(0, top || 0);
      }, 100);
    });
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  methods: {
    /**
     * 跳转客服
    */
    toService() {
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          sceneId: 'cj099'
        }
      })
    },
    /**
    * @description 点击搜索
    */
    hotKeyLink() {
      // if (!this.searchComponentLoad) {
      //   lazySearch().then(() => {
      //     this.searchComponentLoad = true;
      //     this.searchComponentShow = true;
      //   })
      //   return;
      // }
      // this.searchComponentShow = true;
      this.$router.push({
        path: '/goodsSearch',
      })
    },
    /**
     * @description 切换省市区
     */
    locationBtn() {
      // this.showChooseLoaction = true
      if (!this.addrLoad) {
        lazyAddrList().then(() => {
          this.addrLoad = true;
          this.$nextTick(() => {
            this.$refs.addrList.openAddr()
          })
        })
        return;
      }
      this.$refs.addrList.openAddr()
    },
    /**
     * @description 关闭弹窗
     */
    onHide() {
      console.log('hide')
      this.showChooseLoaction = false
      this.$refs.geoLocation.load()
    },
    /**
     * @description 获取地理位置
     */
    handleLocation (location) {
      console.log(location, 'ceshishuju')
      // this.showChooseLoaction = false
      localData.locationData = location
      // this.township = this.locationData.district.substring(0, 6) + '...'
      this.township = localData.locationData.district
      // location = {
      //   region: '山东 青岛 崂山区 中韩街道',
      //   provinceId: 16,
      //   province: '山东',
      //   cityId: 173,
      //   city: '青岛',
      //   districtId: 2450,
      //   district: '崂山区',
      //   townshipId: 12036596,
      //   township: '中韩街道'
      // }
      this.$store.commit('setLocationInfo', location)
    }
  },
  mounted() {
    this.activeCateId = this.$route.query.cateId || '';
    /**
     * @description 获取hot关键词
     */
    defaultSearch({ platform: 3 }).then(res => {
      this.hotwords = res.data.hot_word
      this.skeletonLoading = false
    })
    // store 获取默认定位信息
    this.township = this.$store.state.locationInfo.district
  }
}
</script>

