/**
 * 分类模块
 * 导出至'@/http/service/category'
 */
import http from '../http'
import url from '../url'

// 分类左侧按钮数据
export const categoryLeftnavData = data => {
  return http.request({
    url: url.getNavigations,
    method: 'get',
    params: {
      parentId: 0,
      timer: parseInt(new Date().getTime() / 1000)
    }
  })
}
// 分类左侧按钮数据
export const categoryRightListData = (data) => {
  console.log(data)
  return http.request({
    url: url.getNavigations,
    method: 'get',
    params: {
      parentId: data.id,
      timer: parseInt(new Date().getTime() / 1000)
    }
  })
}
